#secondary .social-icons {
	a {
		img {
			margin-right: 10px;
			opacity: 0.8;
		}
		&:hover, &:active, &:focus {
			img {
				opacity: 1;
			}
		}
	}
}

.entry-content {
	h2 { font-size: 35px; }
	h3.event-title { margin-top: 0; }
}

.uw-hero-image {
	&.opl-header1 {
		background-image: url(../../assets/headers/opl/meany-stage-011.jpg);
		background-position: top center;
	}

	&.opl-header2 {
		background-image: url(../../assets/headers/opl/meany-stage-064.jpg);
		background-position: bottom center;
	}

	&.opl-header3 {
		background-image: url(../../assets/headers/opl/meany-seats-053.jpg);
		background-position: bottom center;
	}

	&.opl-header4 {
		background-image: url(../../assets/headers/opl/meany-stage-side.jpg);
		background-position: center center;
	}
}




